import { Controller } from '@ali/4ever-cangjie';

import {
  isSelectionInOnlyHeading,
  default as isSelectionInHeadingUtil,
} from '../utils/isSelectionInHeading';

import {
  default as isSelectionInAnyHeading,
} from '../utils/isSelectionInAnyHeading'

export function isSelectionInHeading(controller: Controller, type?: string, only?: boolean) {
  if (typeof type === 'string' && typeof only === 'boolean') {
    return isSelectionInOnlyHeading(controller.value, type);
  } else if (type === 'string') {
    return isSelectionInHeadingUtil(controller.value, type);
  } else {
    return isSelectionInAnyHeading(controller.value);
  }
}
